/* eslint-disable */

body {
  font-family: 'Mulish', sans-serif;
  font-size: 18px;
  margin: 0;
  box-sizing: border-box;

  
}

a:link, a:visited {
  color: rgb(255, 255, 255);
  text-decoration: none;
  font-weight: bold;


}

a:hover, a:active {
 text-decoration: none;
}

/*MENU*/

#menu {
  color: rgb(255, 255, 255);
  position: fixed;
  top: 60px;
  right: 85px;
  width: 60px;
  height: 30px;
  cursor: pointer;
  z-index: 10;}


#menu span {
  display: block;
  position: absolute;
  height: 4px;
  background-color: rgb(255, 255, 255);
  left: 0;
  transform: rotate(
0deg
);
  transition: .25s ease-in-out;}

#menu span:first-child {
  width: 100%;
  top: 0;
}

#menu span:nth-child(2) {
  width: 100%;
  top: 10px;
}
#menu span:nth-child(3) {
  width: 80%;
  top: 20px;
  right: 0;
  left: auto;
}

#menu:hover span {
background-color:black;
}



.info {
  position: fixed;
  writing-mode: vertical-rl;
  transform: rotate(180deg);
  margin-top: 30vh;
  padding: 5vw;
  
}

.info span {
  display:inline-block;
  width:0.1vw;
  background-color: rgb(255, 255, 255);
  height:300%;
  
}

.info a:hover {
  color: black;
}


